.item {
  width: 280px;
}
.item img {
  width: 280px;
}
.item p {
  margin: 6px 0px;
}
.item-prices {
  display: flex;
  gap: 29px;
}
.item-price-new {
  color: #374151;
  font-size: 18px;
  font-weight: 600;
}
.item-price-old {
  color: #8c8c8c;
  font-size: 18px;
  font-weight: 500;
  text-decoration: line-through;
}
.item:hover {
  transform: scale(1.05);
  transition: 0.6s;
}

/* MEDIA QUERRY */

@media (max-width: 1280px) {
  .item {
    width: 220px;
    font-size: 14px;
  }
  .item img {
    width: 220px;
  }
  .item-price-old {
    margin: auto 5px;
    font-size: 14px;
  }
}

@media (max-width: 1024px) {
  .item {
    width: 170px;
    font-size: 13px;
  }
  .item img {
    width: 170px;
  }
  .item-price-old {
    font-size: 10px;
  }
  .item-price-new {
    font-size: 13px;
  }
}

@media (max-width: 800px) {
  .item {
    width: 120px;
    font-size: 12px;
  }
  .item img {
    width: 120px;
  }
  .item-prices {
    gap: 20px;
  }
  .item p {
    font-size: 10px;
  }
  .item-price-old {
    font-size: 9px;
  }
  .item-price-new {
    font-size: 11px;
  }
}
@media (max-width: 530px) {
  .item {
    width: 110px;
    font-size: 11px;
  }
  .item img {
    width: 100px;
  }
  .item-prices {
    gap: 15px;
  }
  .item p {
    font-size: 12px;
  }
  .item-price-old {
    font-size: 11px;
  }
}
@media (max-width: 500px) {
  .item {
    width: 160px;
  }
  .item img {
    width: 160px;
  }
}
