.offers {
  width: 65%;
  height: 77vh;
  display: flex;
  margin: auto;
  margin-top: 50px;
  padding: 0px 140px;
  gap: 150px;
  margin-bottom: 100px;
  background: linear-gradient(180deg, #ffe1e1 0%, #e1ffea22 60%);
}
.offers-left {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.offers-left h1 {
  color: #171717;
  font-size: 40px;
  font-weight: 600;
}
.offers-left h2 {
  color: #171717;
  font-size: 30px;
  font-weight: 600;
}
.offers-left p {
  color: #171717;
  font-size: 22px;
  font-weight: 600;
}
.offers-left button {
  width: 230px;
  height: 50px;
  border-radius: 35px;
  background: #ff4141;
  border: none;
  color: white;
  font-size: 18px;
  font-weight: 500;
  margin-top: 10px;
  cursor: pointer;
}
.offers-left button:active {
  background: #eb6f6f;
}
.offers-left button:hover {
  letter-spacing: 1px;
  transition: 0.3s;
}
.offers-right {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-top: 50px;
}
.offers-right img {
  width: 300px;
}

@media (max-width: 1280px) {
  .offers {
    padding: 0px 80px;
    margin-bottom: 120px;
  }
  .offers-left h1 {
    font-size: 40px;
  }
  .offers-left h2 {
    font-size: 30px;
  }
  .offers-left p {
    font-size: 18px;
  }
  .offers-right img {
    width: 300px;
  }
  .offers-left button {
    width: 220px;
    height: 50px;
    font-size: 18px;
  }
  .offers-right {
    padding-top: 30px;
  }
}

@media (max-width: 1024px) {
  .offers {
    padding: 0px 60px;
    margin-bottom: 80px;
    height: 40vh;
    gap: 100px;
  }
  .offers-left {
    margin-left: 20px;
  }
  .offers-left h1 {
    font-size: 35px;
  }
  .offers-left h2 {
    font-size: 25px;
  }
  .offers-left p {
    font-size: 16px;
  }
  .offers-right img {
    width: 170px;
  }
  .offers-left button {
    width: 160px;
    height: 40px;
    font-size: 16px;
  }
  .offers-right {
    padding-top: 10px;
  }
}

@media (max-width: 800px) {
  .offers {
    margin-bottom: 60px;
    gap: 50px;
  }
  .offers-left {
    margin-left: 0px;
  }
  .offers-left h1 {
    font-size: 25px;
  }
  .offers-left h2 {
    font-size: 20px;
  }
  .offers-left p {
    font-size: 13px;
  }
  .offers-right img {
    width: 165px;
  }
  .offers-left button {
    width: 140px;
    height: 30px;
    font-size: 12px;
  }
}

@media (max-width: 500px) {
  .offers {
    height: 25vh;
    margin-bottom: 50px;
    gap: 10px;
  }
  .offers-left {
    margin-left: -20px;
  }
  .offers-left h1 {
    font-size: 16px;
  }
  .offers-left h2 {
    font-size: 16px;
  }
  .offers-left p {
    font-size: 10px;
  }
  .offers-right img {
    width: 100px;
  }
  .offers-left button {
    width: 120px;
    height: 25px;
    font-size: 10px;
    margin-top: 12px;
  }
}
